<template lang="html">
  <div class="myMeetings">
    <link rel="stylesheet" href="https://use.fontawesome.com/releases/v5.0.10/css/all.css" integrity="sha384-+d0P83n9kaQMCwj8F4RJB66tzIwOKmrdb46+porD/OvrJ+37WqIM7UoBtwHO6Nlg" crossorigin="anonymous">



  <h1>myMeetings.vue</h1>
  <br>
  <h5>{{ meetings }}</h5>
  <br>

  <b-container class="createdMeetings">
    <b-card-header header-bg-variant="light" header-text-variant="dark" header-tag="header">
         
        <i class="fab fa-angellist"></i>
    </b-card-header>

    <createdMeetingTemplate
    v-for="(meeting, index) in meetings"
    v-bind:key="meeting.id"
    v-bind:meeting="meeting"
    v-bind:index="index"
    v-on:editmeeting="editMeeting"
    ></createdMeetingTemplate>

    <!--    -->
    <div class="MeetingEditModalForm">
    <!-- <b-modal v-model="showEditingMeeting" centered size="lg" title=" "> -->
      <b-modal centered v-model="showEditingMeeting"
               size="lg" :title="titleEditingModal + meeting.name"
               header-bg-variant="white"
               header-text-variant="dark"
               body-bg-variant="light"
               body-text-variant="dark"
               footer-bg-variant="dark"
               footer-text-variant="light">

        <b-container fluid class="editingData">
          <b-row>
            <b-col>
              <div class="nameMeeting">
                <b-input-group size="">
                    <b-input-group-prepend>
                        <b-btn  variant="dark"> : </b-btn>
                    </b-input-group-prepend>
                    <b-form-input  v-model.trim="meeting.name"
                    type="text">
                  </b-form-input>
                </b-input-group>
              </div>
            </b-col>
          </b-row>
          <br>
          <b-row>
            <b-col cols="2">
              <br>
              :
            </b-col>
            <b-col cols="10">
              <b-form-textarea
                   v-model="meeting.description"
                   :rows="3"></b-form-textarea>
            </b-col>
          </b-row>
          <br>
          <b-row>
            <b-col>
              <b-card
              bg-variant="dark"
                text-variant="white"
                header="  :"
                class="text-center">
                  <b-form-select size="" v-model="meeting.categoryId" :options="options" class="mb-3" :select-size="3" />
            </b-card>
            </b-col>
          </b-row>
          <br>



          <b-row>
            <b-col class="startTimeMeeting">
              <b-input-group size="sm" >
                  <b-input-group-prepend>
                    <b-btn  variant="success"> : </b-btn>
                  </b-input-group-prepend>

                  <b-form-input  v-model="meeting.startTime"
                    type="date">
                  </b-form-input>

              </b-input-group>
            </b-col>
            <b-col class="EndTimeMeeting">
              <b-input-group size="sm" >
                  <b-input-group-prepend>
                    <b-btn  variant="danger"> : </b-btn>
                  </b-input-group-prepend>

                  <b-form-input  v-model="meeting.endTime"
                    type="datetime-local">
                  </b-form-input>

              </b-input-group>
            </b-col>
          </b-row>

          <br>


          <b-row>
            <b-col>
            </b-col>
            <b-col class="maxParticipants" cols="8">
              <b-input-group size="sm" >
                  <b-input-group-prepend>
                    <b-btn  variant="dark">  : </b-btn>
                  </b-input-group-prepend>

                  <b-form-input  v-model.trim="meeting.maxParticipants"
                    type="number">
                  </b-form-input>

              </b-input-group>
            </b-col>
            <b-col>
            </b-col>
          </b-row>
          <br>
          <b-row>
            <b-col>
            </b-col>
            <b-col class="minAge" cols="8">
              <b-input-group size="sm" >
                  <b-input-group-prepend>
                    <b-btn  variant="dark"> : </b-btn>
                  </b-input-group-prepend>

                  <b-form-input  v-model.trim="meeting.minAge"
                    type="number">
                  </b-form-input>

              </b-input-group>
            </b-col>
            <b-col>
            </b-col>
          </b-row>
        </b-container>

      <div slot="modal-footer" class="w-100">
        <p class="float-left"> GoAnyWhere Project 2018</p>
      </div>
            {{ meeting }}
    </b-modal>
  </div>
  </b-container>
<!--
<b-container fluid class="participateMeetings">
       
</b-container> -->
<br>
  </div>
</template>

<script>
import axios from 'axios'
import createdMeetingTemplate from './createdMeetingTemplate'
export default {
  data () {
    return {
      meetings: [
        {
          "id": 1,
          "categoryId": 1,
          "meetingAddress": {
              "id": 4,
              "locality": "-",
              "street": " ",
              "house": "32",
              "latitude": 59.93983170000001,
              "longitude": 30.314559700000018
          },
          "name": "  ",
          "startTime": "1995-03-09",
          "endTime": 200000,
          "description": "     ",
          "maxParticipants": 25,
          "minAge": 12,
          "attachment": null
      },
      {
          "id": 2,
          "categoryId": 2,
          "meetingAddress": {
              "id": 5,
              "locality": "",
              "street": " ",
              "house": "125",
              "latitude": 59.93816109999999,
              "longitude": 30.31553489999999
          },
          "name": "",
          "startTime": 100000,
          "endTime": 200000,
          "description": "  !",
          "maxParticipants": 10,
          "minAge": 14,
          "attachment": null
      }
    ],
    meeting: {name:""},
    titleEditingModal: " : ",
    showEditingMeeting: false,

    options: [
      { value: '1', text: '   ' },
      { value: '2', text: '  ' },
      { value: '3', text: '' },
      { value: '4', text: '  '},
      { value: '5', text: '  '},
      { value: '6', text: ''},
      { value: '7', text: ''},
      { value: '8', text: '  '},
      { value: '9', text: ''},
      { value: '10', text: ''},
      { value: '11', text: ' '},
      { value: '12', text: ''},
      { value: '13', text: ''},
      { value: '14', text: ''},
      { value: '15', text: ''},
      { value: '16', text: ''},
      { value: '17', text: ''},
      { value: '18', text: ''},
      { value: '19', text: ''},
    ]
    }
  },
  methods: {
    editMeeting: function(index){
      this.meeting = this.meetings[index];
      this.showEditingMeeting = true;
    },

  },
    created: function () {
      if (localStorage.getItem('STORAGE_USER_INFO') !== null && JSON.parse(window.localStorage.getItem('STORAGE_USER_INFO')).userId != 0) { //    ,     
           var storageInfo = JSON.parse(window.localStorage.getItem('STORAGE_USER_INFO'));

               axios.get("/meeting/user?id="+storageInfo.userId)
               .then(response => {

                 if (response.status === 200) {
                     this.meetings = response.data;
                     console.log(response);
                    }

               }).catch(function (error) {
                 alert("OOPSSS!! myMeetings    !")
                 console.log(error);
               });

      }
    },

    components:{
      createdMeetingTemplate
    }
}
</script>

<style lang="css">
</style>



